.login{
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  #components-form-demo-normal-login .login-form {
    max-width: 300px;
    margin: 50px auto 0;
  }
  #components-form-demo-normal-login .login-form-forgot {
    float: right;
  }
  #components-form-demo-normal-login .login-form-button {
    width: 100%;
  }

  > div{
    position: absolute;
    z-index: 9;
    width: 100%;
    height: 100%;
  }
  .login_box{
    //background: #343a5b;
    padding: 150px 0 180px;
    text-align: center;
  }
  p{
    //margin-top: 200px;
    color: #343a5b;
    font-size: 14px;
    text-align: center;
    letter-spacing: 8px;
  }
}

.userName{
  cursor: pointer;
  span.name{
    display: inline-block;
    padding-right: 20px;
  }
}

.cur_p{
  cursor: pointer;
}